
.nav {

  ul {
    margin: 0;
    padding: 0;

    li {
      display: block;
      margin-right: 0;
      margin-left: 0;
      @include md {
        margin-left: 1rem;
        display: inline-block; }
      @include md {
        &:first-child {
          margin-left: 0; } }

      > a {
        display: inline-block;
        font-weight: 400;
        font-size: 1.6rem;
        text-transform: lowercase;
        text-decoration: none;
        line-height: 1em;

        &:hover {
          color: $colorBlue; }
        &.offline {
          color: #bbb; } }

      &.current a {
          color: $colorBlue;
          font-weight: 600; } } } }

.overlay {
  z-index: 1;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0,0,0,0);
  transform: translateX(-100%);
  @include transitions(background 0.3s);
  will-change: transform;
  &--visible {
    transform: translateX(0);
    background: rgba(0,0,0,0.3); } }

.nav__wrap {
  position: fixed;
  left: 0;
  top: 44px;
  bottom: 0;
  overflow-y: auto;
  width: 320px;
  background-color: #fff;
  z-index: 2;
  will-change: transform;
  transform: translateX(-100%);
  @include transitions(0);
  @include md {
    position: relative;
    left: auto;
    top: auto;
    bottom: auto;
    overflow: auto;
    width: auto;
    transform: none; }

  &--open {
    transform: translateX(0); }

  ul {
    margin: 0;
    padding: 0;
    li {
      list-style: none;
      border-bottom: 1px solid #ddd;
      @include md {
        border-bottom: none; }

      a {
        display: block;
        padding: 12px;
        &:hover {
          color: $colorBlue; }
        &.offline {
          color: #bbb; } }

      &.current a {
          color: $colorBlue;
          font-weight: 600; } } } }


